$container-1-wight: 400px;
$container-1-height: 200px; 

$container-2-wight: 200px;
$container-2-height: auto;

$container-3-wight: 300px;
$container-3-height: 500px;

.film-description{
  width: 100%;
  display: grid;
  gap: 10px;

  justify-content: center;
  justify-items: center;
  align-items: center;
  align-content: center;

  grid-template:
  [row1-start] "title title title" $container-1-height [row1-end]
  [row2-start] "img text text" $container-2-height [row2-end]
  [row3-start] "img info info" $container-3-height [row3-end]
  / $container-1-wight $container-2-wight $container-3-wight;


  .film-description__name{
    grid-area: title;
    grid-column: span(3);

    font-size: 60px;
    font-weight: lighter;
    margin: 0;

    text-align: center;
  }

  .film-description__poster{
    grid-area: img;
    grid-row: span(2);
    align-self: start;
    
    height: auto;
    width: 350px;
    
    max-width: $container-1-wight;
    max-height: $container-2-height + $container-3-height;
  }

  .film-description__text{
    grid-area: text;
    align-self: start;
    grid-column   : span(2);

    font-size: 20px;
    font-weight: medium;
  }

  .film-description__info{
    grid-area: info;
    align-self: start;
    justify-self: start;
    grid-column: span(2);

    font-size: 25px;

    .film-description__info__name{
      color: $primary-color;
      font-weight: bold;
      width: 200px;
    }

    .film-description__info__param{
      font-weight:lighter;
      height: auto;
    }
  }
}